@import url(https://fonts.googleapis.com/css?family=KoHo|Roboto);
@import url(https://fonts.googleapis.com/css?family=Montserrat);
.header {
  padding-bottom: 10px;
  display: flex;
  align-items: center;
  justify-content: center; }

.header h1 {
  text-align: center;
  font-family: "Montserrat", sans-serif;
  text-transform: uppercase;
  font-size: 28px;
  margin: 20px 10px; }

ul {
  list-style: none;
  position: absolute;
  display: flex;
  text-align: center;
  flex-flow: column;
  width: 80%;
  left: -100%;
  background-color: #fff;
  height: auto;
  z-index: 1;
  margin-top: 300px; }

li {
  text-decoration: none;
  padding: 10px;
  font-family: "KoHo", sans-serif;
  color: #000;
  margin: 60px;
  font-size: 20px;
  border-bottom: 0.5px solid grey; }

.hamburger-container {
  display: flex;
  justify-content: space-between;
  align-items: center; }

.hamburger-checkbox:checked ~ .navbar-list {
  left: 0;
  -webkit-transition: left 0.2s ease-out;
  transition: left 0.2s ease-out; }

.hamburger-container .hamburger-bar1,
.hamburger-container .hamburger-bar2,
.hamburger-container .hamburger-bar3 {
  width: 30px;
  height: 3px;
  background-color: #000;
  margin: 6px 20px; }

.hamburger-checkbox:checked
~ .hamburger-container
.hamburger-label
.hamburger-bar1 {
  -webkit-transform: rotate(-45deg) translate(-9px, 4.5px);
          transform: rotate(-45deg) translate(-9px, 4.5px); }

.hamburger-checkbox:checked
~ .hamburger-container
.hamburger-label
.hamburger-bar2 {
  opacity: 0; }

.hamburger-checkbox:checked
~ .hamburger-container
.hamburger-label
.hamburger-bar3 {
  -webkit-transform: rotate(45deg) translate(-8px, -3.5px);
          transform: rotate(45deg) translate(-8px, -3.5px); }

.hamburger-checkbox {
  display: none; }

#paw-print-header {
  width: 35px;
  margin: 0 20px; }

#home-link {
  color: #d5587f; }

.search-page-wrapper {
  background-image: url("/dog-3.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top;
  height: auto; }

.search-container {
  width: 100%;
  height: 120vh;
  display: flex;
  flex-direction: column;
  background-color: rgba(250, 250, 250, 0.6);
  font-family: "KoHo", sans-serif;
  text-align: center; }

h1 {
  font-family: "KoHo", sans-serif;
  margin-bottom: 15px; }

h3 {
  margin: 0;
  font-family: "KoHo", sans-serif;
  font-size: 20px; }

.search-container-sex,
.search-container-age,
.search-container-size,
.search-container-catOk {
  display: flex;
  padding: 10px;
  font-size: 15px;
  align-items: center;
  justify-content: center; }
  .search-container-sex input,
  .search-container-age input,
  .search-container-size input,
  .search-container-catOk input {
    margin-left: 20px; }
  .search-container-sex label,
  .search-container-age label,
  .search-container-size label,
  .search-container-catOk label {
    margin-left: 10px;
    margin-bottom: 10px; }

.checkbox-container-wrapper {
  margin: 10px;
  display: flex;
  flex-direction: column;
  width: 20%; }

input[type="checkbox"] {
  -ms-transform: scale(1.5);
  /* IE */
  -moz-transform: scale(1.5);
  /* FF */
  -webkit-transform: scale(1.5);
  /* Safari and Chrome */
  -o-transform: scale(1.5);
  /* Opera */
  padding: 10px; }

.submit-button {
  margin: 20px auto 0;
  position: relative;
  width: 170px;
  display: flex;
  justify-content: center; }

.submit-button input,
.submit-button a {
  -webkit-appearance: none;
  padding: 20px 80px;
  background-color: #2e2d2b;
  color: #fff;
  font-size: 20px;
  cursor: pointer;
  margin-bottom: 20px;
  border-radius: 10px;
  border: transparent !important; }

.submit-button #paw-print {
  width: 30px;
  position: absolute;
  top: 20%;
  right: 90%; }

.matches-container {
  padding-bottom: 50px; }

.results-header {
  width: 100%;
  text-align: center; }

.matches-container h1 {
  text-align: center; }

.back-button {
  cursor: pointer;
  margin: 10px; }

@media (min-width: 600px) {
  .header {
    padding-bottom: 10px;
    margin-left: 50px; }
  #paw-print-header {
    width: 50px; }
  .hamburger-container {
    display: flex;
    justify-content: center; }
  .search-container,
  .login-container,
  .aboutus-container {
    width: 40%;
    height: 750px;
    margin: 0 auto;
    border-radius: 20px; }
  .result-page-wrapper {
    width: calc(50% - 40px);
    padding: 0px 10px; }
  .result-page-container {
    margin: 0 10px;
    padding: 2px 0 50px;
    border-bottom: 1px solid black;
    text-align: center;
    height: 100%; }
  .submit-button {
    margin-top: 40px; }
  .image-container {
    height: 60%;
    margin: 0 80px;
    overflow: hidden; }
  .image-container img {
    width: 80%;
    object-fit: cover; }
  .hamburger-label {
    display: none; }
  ul {
    left: 0;
    position: relative;
    display: flex;
    flex-flow: row;
    justify-content: flex-end;
    height: auto;
    padding: 0 20px;
    width: 100%;
    margin: 0; }
  li {
    margin: auto;
    border: none; }
  .results-header {
    text-align: center; }
  .matches-container {
    width: auto;
    height: 100%;
    display: flex;
    flex-flow: row wrap;
    justify-content: center; }
  p {
    font-size: 20px; }
  .detailed-info-container {
    width: 70%; }
  .detailed-info-container img {
    width: 80%; }
  .detailed-info-container p {
    padding: 0 50px; }
  .detailed-info-wrapper h3 {
    font-size: 30px; }
  .contact-container {
    bottom: -20%; }
  .aboutus-container p {
    line-height: 30px;
    margin: 0 30px; }
  .submit-button {
    margin: 20px auto; } }

